import React, { Component, useEffect, useState } from 'react';
import RetroButton from '../retroButton/RetroButton'
//gift
import gif from '../../../../assets/gifts/dance.gif'
//styled
import styled from 'styled-components'
//products
import Products from '../../../../components/Products/Porducts'

import {dbService} from '../../../../DataBase'


const Filter = () => {
    
    const [products,setProducts]=useState([])

    //Button Status
    const [all,setAll] = useState(true)
    const [easy,setEasy] = useState(false)
    const [medium,setMedium] = useState(false)
    const [hard,setHard] = useState(false)

    const fetchProducts = async () => {
        
        const dbProduct = await dbService.collection('product').get();
        dbProduct.forEach((document) => {
            const productObject = {
                ...document.data(),
            }
            setProducts((prev) => [productObject, ...prev]);
        });
           
    }

    function onClick(props) {
        let level = props.target.defaultValue

        if (level === "All"){
            setEasy(false) 
            setMedium(false)
            setHard(false)
        }
        if(level==="Easy"){
            setEasy(current => !current) 
            setMedium(false)
            setHard(false)
            
        }
        if(level==="Medium"){
            setMedium(current => !current) 
            setEasy(false)
            setHard(false)
        } 
        if(level==="Hard"){
            setHard(current => !current) 
            setEasy(false)
            setMedium(false)
        }

        
    
    }

 
 
    const buttons = {

        easy:   <Button onClick={onClick}> <RetroButton text="Easy"  /> </Button> ,
        medium: <Button onClick={onClick}> <RetroButton text="Medium"/> </Button> ,
        hard:   <Button onClick={onClick}> <RetroButton text="Hard"  /> </Button> ,
        free:   <Free   onClick={onClick}> <RetroButton text="Free"  /> </Free>   ,
        paid:   <Paid   onClick={onClick}> <RetroButton text="Paid"  /> </Paid>   ,
        all:    <All    onClick={onClick}> <RetroButton text="All"   /> </All>    ,
    }

        useEffect(() => {
            fetchProducts();
          }, [])

          function renderProducts(dish){
        
            return <Products 
                        id={dish.id} 
                        title={dish.title} 
                        description={dish.description}
                        img={dish.img} 
                        level={dish.level} 
                        time={dish.time}
                        price={dish.price}
                        discount={dish.discount}/>
          }
     

        return (  <Page>

                    <Title> Proyects </Title>
                         <Hr><hr></hr></Hr>
                 
                    <Level>    
                        {buttons.easy}
                        {buttons.medium}
                        {buttons.hard} 
                        {buttons.all}
                    </Level>
                    
                    <DivMostrador>
                        <Mostrador className="easy">All</Mostrador>
                    </DivMostrador>

                    <InfoDiv>
                        <img className="gif" alt="nft"  src={gif}/>
                    </InfoDiv>

                    <FreePaid>
                        {buttons.free}
                        {buttons.paid}  
                    </FreePaid>
                    <Filtro>
                        {!easy && !medium && !hard ? products.map(renderProducts):<h1>null</h1> }
                    </Filtro>
                    
                    
                
            </Page>

         );

}
 
export default Filter;



