import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
//icons
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PlayIcon from '../../assets/icons/play.png';
import { PowerInputSharp } from '@material-ui/icons';


const Products = (props) => {
    
        return ( 
            
            <Product id={props.id}>

                <img className="play" src={PlayIcon}/>

                <img className="video" src={props.img}/>

                <div className="div">
                    <Link to="/" className="Link"style={linkStyle}>

                        <Level className={props.level}> {props.level}</Level>

                        <h1 className="Titulo">{props.title}</h1>
                        
                    </Link>

                    <Descripcion>
                        
                        <p className="Descripcion">{props.description}</p>
                       

                    </Descripcion>
                    <Time> <p className="Time"><b>Time: </b> {props.time} min.</p></Time>
                    <div className="sale"><h1>SALE</h1></div>
                
                    <p>{props.discount}% Disc. <b className="b">${props.price} USD</b></p>

                    <PriceButton className="piceButton">

                        <div className="icon"><ShoppingCartIcon className="ICON" /></div>

                        <div className="price" ><h1>$ {(props.price - (props.price * props.discount/100)).toFixed(2)}</h1> <h1 className="usd">USD</h1> </div>

                    </PriceButton>

                </div>
               
            </Product>

         );
    
}
 
export default Products;


const Descripcion = styled.div`
    
    margin: 60px 0 0px 0px;
    height: 80px;
    overflow: hidden;
  
    .Descripcion{
        color:#000 !important;
    }

`;
const Time = styled.div `
.Time{
    color:#000;
    margin:0 0 3px 0;
}
`;
const linkStyle ={
    textDecoration: 'none',
    color:'#161616',
    background:"#fff",
}
const Product = styled.div`
overflow: hidden;
height:390px;
min-height:183px;
width: 221.6px;
margin: 10px;
background-color:#fff;
display:inline-block;
@media (min-width: 768px) {
    margin: 10px 16px 0px 16px;
    
}
.piceButton{
    color:#fff;
    background-color:#00b2bd;
}
.div{
    padding:10px 15px 20px 15px;
}
.Link{
    padding:0px 0 0 0;
    position:relative;
    display: inline-block;
    float:right;
    width:100%;
    textDecoration: 'none';
    font-size:8px;
    line-height:25.99px;
    text-align:left;
    font-family: 'Lato', sans-serif;
    .Titulo:hover{
        color:#da6964;
        
    }
}

.video{
   
    min-width: 221.6px;
    width:45px;
    height:33.33%;
  
}
.play:hover{
    background-color:#000;
    opacity:0.5;
   
}
.play{
    width:45px;
    padding:45px 90px 40px 85px;
    
    margin: 0px 0px 0px 0px;
    position: absolute;
}
.easy{
    background-color: #908fbb;
    font-size:10px;
   
}
.medium{
    background-color: #4ea6a6;
    font-size:10px;
    
    
}
.hard{
    background-color: #da6964;
    
}
.sale{
    display:inline-block;
    padding:1px;
    width:100%;
    border-radius: 2px;
    float:left;
    margin: 0 0 5px 0px;

    h1{
        width:37px;
    background-color: #f02d00;
        color:#fff;
        font-size:11px ;
        text-align: center;

    }
  
}
p{
    color:red;
    font-size:13px ;
    text-align: left;
    .b{
        text-decoration: line-through 1px;
    }
    
}


`;
const Level = styled.div`
line-height:20.99px;
display: inline-block;
font-family: "Press Start 2P";
width:72.4px;
float:left;
height:18.4px;
text-align: center;
margin: 3px 5px 0px 0px;
padding: 0px 0px 0px 0px;
color: #fff;
text-shadow: 1.5px 1px #000;
font-size:11px;
border-radius: 2px;
background-color:#f6ca00;
`;
const SkillDiv = styled.div`
background-color:;
position:relative;
display: inline-block;
float:right;
width:100%;
height:100px;
list-style: 'none';
text-align: left;
padding: 0 0px 0px 0px;
margin:0 0 0px  0px;
h2{
    font-size:14px ;
    padding: 5px 0 0 0;
    margin: 0 0px 0px 10px;
}


`;

const PriceButton = styled.button`

outline: none;
height:28px;
width:191.6px;
border:none;
margin-top:4px;

padding: 2px 0 0 0;
div{
    display:inline-block;
    margin:;
    margin-left:33px ;
    height:100%;
}
.icon{
    float:left;
    
    height:20px;
    width:auto;
    .ICON{
        height:20px;
        margin-top:2px ;
    }
}
.price{

    height:100%;
    width:auto;
    margin-left:3px ;
    display:inline-block;
    float:left;
    letter-spacing: 1px;
    padding:0px 0px 0px 0px;
    font-family: 'Montserrat', sans-serif;
    h1{
        font-size:17px;
        color:#fff;   
        display:inline-block;
    }
    .usd{
        letter-spacing: 0px;
    }
}

`;
